const apiKey = 'SanVVsR5d1aXooIHsc7RzallrOOh7ruH';
        const baseApiUrl = 'https://api.nytimes.com/svc/topstories/v2/';
        let articles = []; 

        async function fetchArticles(section) {
            const apiUrl = `${baseApiUrl}${section}.json?api-key=${apiKey}`;
            try {
                const response = await fetch(apiUrl);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                articles = data.results; 
                displayArticles(section, articles); 
            } catch (error) {
                console.error('There was a problem with the fetch operation:', error);
            }
        }

        function displayArticles(section, articlesToDisplay) {
            const container = document.getElementById('articles-container');
            container.innerHTML = ''; 

            const sectionTitle = document.createElement('h2');
            sectionTitle.className = 'section-title';
            sectionTitle.textContent = section.charAt(0).toUpperCase() + section.slice(1); 
            container.appendChild(sectionTitle);

            const gridContainer = document.createElement('div');
            gridContainer.className = 'grid-container';

            articlesToDisplay.forEach(article => {
                const articleElement = document.createElement('article');
                const imageUrl = article.multimedia.length > 0 ? article.multimedia[0].url : ''; 
                articleElement.innerHTML = `
                    ${imageUrl ? `<img src="${imageUrl}" alt="${article.title}">` : ''}
                    <h3>${article.title}</h3>
                    <p>${article.abstract}</p>
                    <a href="${article.url}" target="_blank">Read more</a>
                `;
                gridContainer.appendChild(articleElement);
            });

            container.appendChild(gridContainer);
        }

        fetchArticles('arts');